.sofax-hero-thumb {
    position: relative;
    padding-top: 80px;
    @include max(991px) {
        padding-top: 60px;
        text-align: center;
        img {
            width: 100%;
        }
    }
    @include max(767px) {
        padding-top: 40px;
    }
}
//  end sofax hero thumb v1

.sofax-content-img {
    position: relative;
    @include max(991px) {
        text-align: center;
        margin-bottom: 40px;
        img {
            width: 100%;
        }
    }
}
.mr-90 {
    @include max(991px){
        margin-right: 0;
    }
}
.ml-60 {
    @include max(991px){
        margin-left: 0;
    }
}
.ml-70 {
    @include max(991px){
        margin-left: 0;
    }
}
// end sofax content thumb v1

