.sofax-portfolio-author {
    &-wrap {
        display: flex;
        justify-content: space-between;
        margin-bottom: 30px;
    }
    &-data{
        h4 {
            margin-bottom: 5px;
        }
    }
    &-icon{
        align-self: center;
        @include max (991px){
            img {
                width: 80%;
            }
        }
        @include max (767px){
            img {
                width: 70%;
            }
        }
    }
}