.sofax-footer-titlev5 {
    padding: 100px 0;
    border-bottom: 1px solid var(--light-color);
    @include max (991px){
        padding: 80px 0;
    }
    @include max (767px){
        padding: 60px 0;
    }
}
.sofax-footer-title-btn{
    &-wrap {
        @include max(991px){
            display: flex;
            justify-content: center;
            width: 100%;
            margin-top: 30px;
        }
    }
    &-icon {
        padding: 15px;
        border: 1px solid var(--dark-bg);
        width: 80px;
        height: 80px;
        border-radius: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        &:hover {
            border: 1px solid var(--accent-color);
            background-color: var(--accent-color);
        }
        @include max (1199px){
            width: 70px;
            height: 70px;
         }
        @include max (991px){
           width: 60px;
           height: 60px;
        }
    }
}
