/*  sofax services section v3 */
.sofax-service-iconbox {
    &-wrap {
        display: flex;
        background-color: #fff;
        border-radius: 5px;
        padding: 30px;
        margin-bottom: 24px;
        &.version7{
            border: 1px solid var(--light-color2);
        }
    }
    &-data h4 {
        margin-bottom: 15px;
    }
    &-data p {
        font-size: 20px;
    }
    &-icon {
        flex-shrink: 0;
        @include max(767px){
            flex-shrink: 1;
        }
    }
    
}
// sofax services v4
.sofax-service-table{
    &-item{
        display: flex;
        padding: 35px 0;
        border-bottom: 1px solid #EAEDF0;
        &:first-child{
            padding-top: 0;
        }
        &:last-child{
            padding-bottom: 0;
            border: none;
        }
        @include max(991px) {
            display: block;
        }
        
    }
    &-title{
        flex: 0 0 36%;
        h3{
            margin: 0;
            font-size: 35px;
            @include max(1199px) {
                font-size: 30px;
            }
        }
    }
    &-body{
        flex: 0 0 45%;
        padding: 0 30px;
        @include max(991px) {
            padding: 24px 0;
        }
        p{
            font-size: 20px;
        }
    }
    &-btn{
        flex: 0 0 19%;
        text-align: right;
        @include max(991px) {
            text-align: left;
        }
        .sofax-default-btn.outline-btn{
            padding: 17px 60px;
        }
        &:hover  .sofax-default-btn.outline-btn{
            background-color: var(--accent-color);
            border: 1px solid var(--accent-color);
        }
    }

    &-titlev5 {
        flex: 0 0 42%!important;
        @include max(1199px) {
            font-size: 30px;
        }
    }
    &-bodyv5{
        flex: 0 0 46%;
        padding: 0 38px!important;
        @include max(991px) {
            padding: 24px 0!important;
        }
        p{
            font-size: 20px!important;
        }
    }
    &-btnv5{
        flex: 0 0 12%!important;
        display: flex;
        justify-content: end;
        @include max(991px) {
            justify-content: start;
            display: block;
        }
        .p15{
            padding: 15px !important;
            width: 80px !important;
            height: 80px !important;
            display: flex;
            justify-content: center;
            align-items: center;
            @include max (1199px){
                width: 65px!important;
                height: 65px!important;
            }
            @include max (991px){
                width: 60px!important;
                height: 60px!important;
            }
        }
    }
}
