.sofax-hero{
    &-section{
        padding-top: 205px;
        @include max(991px) {
            padding-top: 150px;
        }
        @include max(767px) {
            padding-top: 150px;
        }
        @include max(575px) {
            padding-top: 120px;
        }
    }
    
    &-content{
        span{
            margin-right: 12%;
        }
        &.center{
            text-align: center;
            max-width: 1074px;
            margin: 0 auto;
            margin-bottom: 50px;
            @include max(1199px) {
                max-width: 700px;
            }
            &.max-width-medium {
                text-align: center;
                max-width: 1100px;
                margin: 0 auto;
            }
        }
        &.center-center{
            text-align: center;
            max-width: 1200px;
            margin: 0 auto;
            margin-bottom: 50px;
        }
        p{
            font-size: 20px;
        }
        
        @include max(991px) {
            text-align: center;
            @include max(1199px) {
                max-width: 650px;
                margin: 0 auto;
            }
        }
    }
}
.sofax-rating-icon  {
    text-align: center;
    padding-top: 15px;
    ul {
        li {
            display: inline-block;
            font-size: 18px;
            line-height: 28px;
            &:last-child{
                margin-left: 5px;
            }
        }
    }
}
//  end hero section v1

.sofax-app-wrap {
    display: flex;
    align-items: center;
    gap: 24px;
    flex-wrap: wrap;
    @include max (991px){
        justify-content: center;
    }
}
